:root
{
  --main-background:  #f8f8f5;
}

body {
  background-color: #f8f8f5;
  background-color: var(--main-background);
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  margin-bottom: 200px;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

:root {
    --logo-color: rgb(56, 51, 35);
    --nav-links-color: #34250b;
    --background-color: #f8f8f5;
}

.navbar {
    display: flex;
    justify-content: space-between;
    align-items: center;
    background-color: #f8f8f5;
    background-color: var(--background-color);
    color: rgb(56, 51, 35);
    color: var(--logo-color);
    padding: 20px;
    text-align: right;
    padding-right: 40px;
}
  
.nav-link {
    margin-right: 15px;
    margin-left: 15px;
    color: #34250b;
    color: var(--nav-links-color);
    text-decoration: none;
    font-size: 110%;
}

/* underlined when active */
.nav-link.active::before {
    -webkit-transform: scaleX(1.125);
            transform: scaleX(1.125);
}


.logo {
    text-decoration: none;
    color: inherit; 
    padding-top: 40px;
    padding-left: 40px;
    text-align: left;
    text-align: bottom;
    margin-right: 60px;
    padding-bottom: 10px;
}

ul {
    padding-top: 40px;
    list-style-type: none;
}

li {
    display: inline-block;
    margin-right: 10px; /* add spacing between items */
}

/* Underline hover animation */
a {
    position: relative;
    text-decoration: none;
}
  
.nav-link:hover {
    color: #34250b;
    color: var(--nav-links-color);
}
  
.nav-link::before {
    content: "";
    position: absolute;
    display: block;
    width: 84%;
    height: 1px;
    bottom: -3px;
    left: 8%;
    background-color: #34250b;
    background-color: var(--nav-links-color);
    -webkit-transform: scaleX(0);
            transform: scaleX(0);
    transition: -webkit-transform 0.35s ease;
    transition: transform 0.35s ease;
    transition: transform 0.35s ease, -webkit-transform 0.35s ease;
}
  
.nav-link:hover::before {
    -webkit-transform: scaleX(1);
            transform: scaleX(1);
}

/* Dropdown menus */

.dropdown {
    position: relative;
    display: inline-block;
    border-radius: 25px;
  }
  
/* hide by default */
.dropdown-content {
    display: none;
    position: absolute;
    background-color: #f9f9f9;
    min-width: 160px;
    box-shadow: 0px 2px 2px 1px rgba(0,0,0,0.2);
    z-index: 1;
    text-align: center;
    border-radius: 20px;
    left: 50%;
    -webkit-transform: translateX(-50%);
            transform: translateX(-50%);
    top: 100%;
}
  
.dropdown-content a {
    color: #34250b;
    color: var(--nav-links-color);
    padding: 12px 16px;
    text-decoration: none;
    display: block;
    transition: none;
}

.dropdown-content a:hover {
    background-color: #ebebeb;
    border-radius: 20px;
}
  
/* display on hover */
.dropdown:hover .dropdown-content {
    display: block;
}
  
:root {
  --nav-links-color: #34250b;
}

.gallery-banner img {
  padding-top: 20px;
  width: 100vw;
  max-height: 30vh; /* Set the maximum height to 30% of the viewport height */
  object-fit: cover; /* Maintain the aspect ratio and cover the container */
  object-position: center 30%;
  padding-bottom: 40px;
}

.gallery-header {
  font-family: 'Exo 2, sans-serif';
  font-weight: 400;
  letter-spacing: 0.5px;
  text-align: center;
  line-height: 1.5;
  font-size: 2em;
  color: #34250b;
  color: var(--nav-links-color);
  border-bottom: 6px double #34250b;
  border-bottom: 6px double var(--nav-links-color);
  margin-left: 20%;
  margin-right: 20%;
  margin-bottom: 40px;
}

.gallery-container {
  display: flex;
  flex-wrap: wrap;
  justify-content: center; /* Center items horizontally */
  align-items: flex-start; /* Align items at the top */
  margin-left: 40px;
  margin-right: 40px;
}

.gallery-row {
  display: flex;
  width: 100%;
}

.gallery-item {
  flex-grow: 0;
  flex-shrink: 0;
  flex-basis: calc(33.333% - 20px); /* Allow items to grow up to their max-width */
  margin-bottom: 20px; /* Add spacing between rows */
  margin-right: 10px; /* Add spacing between items */
  overflow: hidden; /* Ensure images don't overflow their containers */
  display: flex; /* Use flexbox to match image heights */
  flex-direction: column; /* Stack image and description vertically */
}

.gallery-item img {
  flex: 1 1; /* Allow images to grow vertically within the flex container */
  width: 100%; /* Ensure the image width scales with the container */
  height: auto; /* Maintain the image's aspect ratio */
}

/* Contact.css */
.contact-container {
    position: relative; /* Position the container relative to its contents */
    width: 100%;
    max-width: 40vw; /* Adjust the max-width as needed */
    max-height: 90vw; /* Adjust the max-width as needed */
    margin: 0 auto; /* horizontal */
    padding: 20px;
    box-shadow: 0px 0px 6px 2px rgba(33, 33, 31, 0.319); /* Add a shadow for depth */
    border-radius: 20px; /* Adjust the border-radius for rounded corners */
    background-color: #f9f9f9; /* dropdown menu color */
    color: #34250b; /* navbar text color */
    text-align: center;
    margin-top: 40px;
}

.header-message
{
    color: #34250b; /* navbar text color */
    text-align: left;
    margin-left: 20px;
    margin-right: 20px;
}
  
/* Style the form elements */
.form-container {
    text-align: left;
    display: flex;
    flex-direction: column;
    grid-gap: 10px;
    gap: 10px;
    padding: 20px;
}

/* Add styles to arrange First name and Last name side by side */
.name-group {
    display: flex;
    grid-gap: 10px;
    gap: 10px; /* Adjust the gap as needed */
}

.name-group .form-group {
    flex: 1 1; /* Allow First name and Last name to take equal width */
}


.form-group {
    display: flex;
    flex-direction: column;
    position: relative;
}

.required-label {
    font-size: small;
    opacity: 0.6;
}


label {
    font-weight: bold;
}

input[type="text"],
textarea {
    padding: 10px;
    border: none;
    border-radius: 5px;
    background-color: #efeeea5b; /* Add a semi-transparent background to form fields */
    border: 1px solid #432c0441; /* Outline the text boxes */
}

button[type="submit"] {
    background-color: #625a13; /* button color */
    color: #f9f9f9; /* button text */
    font-size: medium;
    padding: 10px 20px;
    border: none;
    border-radius: 5px;
    cursor: pointer;
}
  
